.advantages {
  padding: 118px 0 60px;

  background-color: $color-ghost-white;

  @include vp-1199 {
    padding: 99px 0 51px;
  }

  @include vp-767 {
    padding: 69px 0 34px;
  }

  &__title {
    margin: 0 0 10px;

    text-align: center;

    @include vp-1199 {
      margin: 0 0 25px;
    }

    @include vp-767 {
      margin: 0 0 38px;
    }
  }

  &__list {
    display: grid;
    margin: 0;
    padding: 0 60px;

    list-style: none;
    column-gap: 60px;

    grid-template-columns: 1fr min-content 1fr;
    grid-template-rows: max-content min-content;

    @include vp-1199 {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      max-width: 678px;
      margin: 0 auto;
      padding: 0;

      column-gap: 2%;
    }

    @include vp-767 {
      flex-direction: column;

      column-gap: 0;
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;
    min-height: 213px;
    padding: 49px 0;

    column-gap: 30px;

    @include vp-1199 {
      width: 47.9%;
      min-height: auto;
      margin-bottom: 21px;
      padding: 22px 0;
    }

    @include vp-767 {
      flex-direction: column;
      width: 100%;
      margin-bottom: 28px;
      padding: 0;
      align-items: center;

      text-align: center;
    }

    &:nth-child(1n + 5),
    &:nth-child(1n + 4) {
      padding-top: 73px;

      border-top: 2px solid rgba($color-crimson, 0.2);

      @include vp-1199 {
        order: 2;
        padding-top: 22px;

        border: none;
      }

      @include vp-767 {
        padding-top: 0;
      }

      .advantages__text-wrapper {
        padding-top: 0;
      }

      dt {
        line-height: 66px;

        @include vp-1199 {
          line-height: 52px;
        }
      }
    }

    &:nth-child(1n + 5) {
      padding-left: 2px;

      @include vp-1199 {
        padding-left: 0;
      }
    }

    &:nth-child(2n) {
      justify-content: flex-start;
      padding-left: 20px;

      @include vp-1199 {
        padding: 22px 0;
      }

      @include vp-767 {
        justify-content: center;
        padding: 10px 0;
      }
    }

    &:nth-child(2n - 1) {
      justify-content: flex-end;

      @include vp-1199 {
        justify-content: flex-start;
      }

      @include vp-767 {
        justify-content: center;
      }
    }

    &:first-child {
      min-width: 240px;
      min-height: 307px;
      margin-top: 60px;
      padding: 46px 34px 53px 32px;
      grid-column: 2/3;
      grid-row: 1/-1;
      justify-self: center;
      align-self: start;

      text-align: center;
      color: $color-default-white;
      column-gap: 0;

      background-color: $color-resolution-blue;
      border: none;

      @include vp-1199 {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        order: 1;
        min-width: 65.3%;
        max-width: auto;
        min-height: 239px;
        margin: 0 auto 23px;
        padding: 35px 60px;

        transform: none;
      }

      @include vp-767 {
        position: relative;
        z-index: 1;

        justify-content: flex-start;
        width: 100%;
        max-width: calc(442px - 10%);
        margin: 15px auto 34px auto;
        padding: 46px 60px 40px;
      }

      &::before {
        @include vp-767 {
          content: "";
          position: absolute;
          top: 0;
          left: 50%;
          z-index: -1;

          width: 110%;
          height: 100%;

          background-color: $color-resolution-blue;
          transform: translateX(-50%);
        }
      }

      h3 {
        margin-bottom: 33px;

        font-size: 26px;
        line-height: 37px;

        @include vp-1199 {
          display: inline-flex;
          flex-direction: column;
          margin-bottom: 15px;

          font-size: 22px;
          line-height: 33px;
        }

        @include vp-767 {
          margin-bottom: 10px;
        }
      }

      p {
        line-height: 24px;

        @include vp-1199 {
          width: 100%;
          padding: 0 60px;
        }

        @include vp-767 {
          padding: 0;
        }
      }
    }
  }

  &__text-wrapper {
    padding-top: 5px;

    @include vp-1199 {
      margin-top: -2px;
      padding-top: 0;
    }
  }

  &__item-title {
    margin: 0 0 5px;

    font-weight: 700;
    font-size: 22px;
    line-height: 33px;

    @include vp-1199 {
      margin: -2px 0 3px;

      font-size: 20px;
      line-height: 30px;
    }

    @include vp-767 {
      margin: 0 0 3px;
    }

    span {
      font-size: 74px;
      line-height: 80px;

      @include vp-1199 {
        margin-left: -5px;
        line-height: 75px;
      }

      @include vp-767 {
        line-height: 62px;
      }
    }
  }

  &__text {
    margin: 0;

    font-weight: 300;
    line-height: 22px;

    @include vp-1199 {
      width: 227px;
    }
  }

  &__numbers {
    order: -1;
    margin: 0;

    @include vp-767 {
      margin: 0 0 7px;
    }

    dt {
      font-weight: 700;
      font-size: 71px;
      line-height: 68px;
      color: $color-sapphire;

      background: linear-gradient(142.51deg, $color-crimson 0%, $color-cobalt 87.33%);
      background-clip: text;

      @include vp-1199 {
        font-size: 58px;
        line-height: 50px;
      }

      @include vp-767 {
        line-height: 51px;
      }

      -webkit-text-fill-color: $color-transparent;
    }

    &--second {
      margin-left: 6px;

      @include vp-1199 {
        margin-left: 0;
      }

      // dt {
      //   background: linear-gradient(127.61deg, #D10A42 0%, #0041CA 45.58%);
      //   -webkit-text-fill-color: $color-transparent;
      //   background-clip: text;
      // }
    }

    dd {
      margin: 0;

      font-size: 14px;
      line-height: 21px;
      color: $color-persian-blue;
    }
  }
}
